.searchbtn {
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 8px;
    padding-bottom: 8px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    top: 0px; /*   */
    z-index: 10; /*   */
    position: sticky; /*   */
    background-color: white;
    transition: box-shadow 0.3s ease; /*         */
    box-shadow: none; /*     */
}

.Search.sticky .searchbtn {
    box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.1); /*     */
}

