#login,
#join{
    background-color: white;
    #wrapper{
        width: 500px ;
        margin: 0 auto;

        // test
        height: 100vh;
        // background-color: greenyellow;
        #head{
            display: flex;
            height: auto;
            margin-top: 2vh;
            //  
            align-items: center;
            background-color: white;

            .left{
                width: 50%;
                text-align: left;

                h1{
                    //  (hover) 
                    display: inline;
        
                    font-weight: bold;
                    font-size: 23px;
                    padding: 0 0 0 25px;
                    color: #9ACD32;
                }
                h1:hover {
                    color: rgb(0, 204, 0);
                    // color: #6B8E23;
                    cursor: pointer; //     
                    transition: color 0.3s, transform 0.3s; //   
                }
            }

            .right{
                width: 50%;
                text-align: right;
                margin: 0 10px 0 0;

                button{
                    padding: 5px 12px;
                    font-size: 15px;

                    border-width: 0.1vh;
                    border-style: solid;
                    border-color: gray;
                    border-radius: 0.5vh;

                    //      
                    cursor: pointer;
                    // hover 
                    transition: background-color 0.3s ease;
                }
                button:hover {
                    background-color: rgb(169, 169, 169);
                }

            }

            
        }

        
        #main{
            // margin-top: 2vh;
            width: 100%;
        
            background-color: white;
        
            .join_form{
                // display: flex;
                //  
                flex-direction: column;
                //  
                align-items: center;
                // box-sizing: border-box;
                
                //   
                // padding: auto;
                padding: 1vh 10px 0 10px;
                margin-top: 15px;
                height: 20vh;

                h2 {
                    margin-bottom: 2.5vh;
                    font-size: 10vh;
                    color: #333;
                
                    // position: left;
                    font-weight: bold;
                    font-size: 3.5vh;
                }
            }
            
            .textbox{
                display: flex;
                align-items: center;

                // 100 - 6, 4
                width: 100%;
                // padding: 3%;
                // margin: 0 30px 0 30px;
                margin-bottom: 1.5vh;
            
                // font-size: 15px;
                // border-width: 0.1px;
                // border-radius: 10px;
                // border-style: solid;
                // border-color: gray;

                .imgbox{
                    width: 10%;
                    > img{
                        /*    */
                        padding-right: 1vw;
                
                        /*    */
                        height: 3vh;
                        width: 3vh;
                    }
                }
                //   
                .inputbox {
                    // height: 100%;
                    width: 90%;

                    padding: 3%;
                    // margin: 0 30px 0 30px;
                
                    font-size: 15px;
                    border-width: 0.1px;
                    border-radius: 10px;
                    border-style: solid;
                    border-color: gray;
                }

            }




            .checkbox {
                margin-top: 10px;
                p{
                    padding: 5%;
                }

                .left{
                    font-size: 15px;
                    font-weight: bold;
                    
                    width: 100px;
                }
                // .right{
                //     margin: 10px 0;
                // }
                .spacer {
                    height: 20px; //    
                }
            }

            //  
            .buttonbox{
                .left, .right{
                    width: 50%;
                }

            }
            
            .button_login{
                display: flex;
                // width: 90%;
                margin-bottom: 1.5vh;
            }

            .button_2{
                display: flex;
                width: 100%;
                // padding: 0.5vw;
            }

            button {
                height: 4.5vh;
                width: 100%;
                // padding: 0.5vw;
                margin: 0vh 2% 5px 2%;
                
                border-width: 0.1vh;
                border-style: solid;
                border-color: gray;
                border-radius: 0.5vh;
            
                // background-color: #007bff;
                // color: #fff;
                font-size: 18px;
                // font-weight: bold;
            
                //      
                cursor: pointer;
                
                // hover 
                transition: background-color 0.3s ease;
            }
            
            
            button:hover {
                background-color: rgb(169, 169, 169);
            }
        }
    }
}
