#mypage,
#order{
    background-color: white;

    // - - - Header - - -
    #head{
        //    
        display:table;
        line-height: 50%;

        width: 100%;
        max-width: 100%;
        height: 5vh;
        // skyblue rgb : 135, 206, 235
        background-color: rgba(135, 206, 235, 0.3);

        // padding:0 1vw 0 1vw;
        > .left{
            //    
            display:table-cell;
            vertical-align: middle;
    
            width: 30%;
            height: 100%;
            padding-left: 18px ;
            h1{
                font-size: 15px;
            }
            // h1{
            //     //  (hover) 
            //     display: inline;
    
            //     font-weight: bold;
            //     font-size: 23px;
            //     padding: 0 0 0 25px;
            //     color: #9ACD32;
            // }
            // h1:hover {
            //     color: rgb(0, 204, 0);
            //     // color: #6B8E23;
            //     cursor: pointer; //     
            //     transition: color 0.3s, transform 0.3s; //   
            // }
        }
        .middle{
            width: 40%;

            //    
            display:table-cell;
            vertical-align: middle;

            //  
            text-align: center;


        }
        > .right{
            width: 30%;
            height: 100%;

            //    
            display:table-cell;
            vertical-align: middle;
    
            //  
            text-align: right;
    
            > img{
                //   
                padding-right: 1vw;
        
                //   
                height: 28px;
                width: 28px;
            }
        }
    }

    //  
    h2{
        // 
        font-weight: 600;
        font-size: 15px;
        color: rgba(128, 128, 128, 0.7);
        padding: 1vh 0;
        vertical-align: middle;
    }
    h2:hover{
        //   
        cursor: pointer;
        // 
        text-decoration: underline;
    }

    h1{
        /*  */
        font-weight: 600;
        font-size: 1.5vh;
        color: rgba(128, 128, 128, 0.7);
        padding: 1vh 0;
        vertical-align: middle;
    }

    p{
        // 
        font-weight: 1000;
        font-size: 1.8vh;
        color: rgba(0, 0, 0, 1);
        padding: 1vh 0;
        vertical-align: middle;

    }

    #main{
        height: 100%;
        // width: 700px;
        padding: 0 2vw 0vh 2vw;
        
        background-color: white;
        .fst{
            //  
            display: flex;
            // 
            align-items: center;
            // 
            // justify-content: center;
            padding: 1vh 0;
            height: 10vh;
            width: 100%;
            background-color: white;
            > img{
                //  
                padding: 0 4vw 0 0vw;
        
                //   
                height: 50px;
                width: 50px;
            }
            .middle{
                width: 82%;
            }
            .right{
                //  
                display: flex;
                // 
                align-items: center;
                /*  */
                justify-content: flex-end; 
                // float: left;
                
                //  (.fst)  .right width 100% 
                // .middle      ,      100%  
                flex: 1;
                
                height: 100%;
                // width: 7vw;
                > img{
                    //   
                    margin-right: 10px ;
                    height: 30%;
                    // width: 80%;
                }
            }
        }

        .scd{
            //  
            display: flex;
            // 
            align-items: center;
            // 
            // justify-content: center;
            
            height: 10vh;
            margin: 1vh 0.5vw;
                
            //  ,  
            border: 0vh solid gray;
    
            //  
            border-radius: 1rem;
    
            //  
            box-shadow: 0 1px 5px rgba(0, 0, 0, 0.3);
    
            //    
            background:
            linear-gradient(to right, 
                transparent 0, 
                rgba(0, 0, 0, 0.5) 1px, 
                //  
                transparent 2px) 33.333% 50%,
            linear-gradient(to right, 
                transparent 0, 
                rgba(0, 0, 0, 0.5) 1px, 
                transparent 2px) 66.666% 50%;
            background-size: 1px 50%; /*     */
            background-repeat: no-repeat;
            .left,.middle,.right {
                //  
                display: flex;
                //  
                flex-direction: column; //   
                align-items: center; //   
                justify-content: center; //   
        
                width: 33.333%;
            }
        }

        .text{
            padding: 2vh 0 2vh 0.5vw;
            > img{
                //   
                height: 10vh;
                width: 10vh;
            }
        }

        .ad{
            //  
            display: flex;
            // 
            align-items: center;
            // 
            justify-content: center;
    
            height: 11vh;
            margin: 0vh 0.5vw;
            background-color: rgba(144, 238, 144, 0.4); 
            
            //  ,  
            border: 0vh solid gray;
    
            //  
            border-radius: 1rem;
        }

        .ad2{
            //  
            display: flex;
            //   
            flex-direction: column;
            // align-items: center;
            justify-content: center;
    
            height: 10vh;
            margin: 0vh 0.5vw;
            padding-left: 25px;
            background-color: rgb(220, 220, 220); 
            
            //  ,  
            border: 0vh solid gray;
    
            //  
            border-radius: 1vh;
            p{
                font-size: 1.6vh;
                color: rgba(10, 10, 10, 1);
                vertical-align: middle;
            }
        }

        .last{
            //  foot     
            padding: 2.5vh 0 10vh 0.5vw;
        }

        // --- order  
        .ordertable{
            //  
            // display: flex;
            // 
            // align-items: center;
            // 
            // justify-content: center;
            width: 100%;
            background-color: white;

            margin: 20px 0;
            th, td, tr {
                //   
                border: 1px solid #ddd;
                //   
                padding: 8px;
                //  
                text-align: center;
                vertical-align: middle;

                //  
                white-space: nowrap;
            }

        }
    }

    #foot {
        // width: 700px;
        //  
        display: flex;
        // 
        align-items: center;
        // 
        // justify-content: center; 

        //  ,  
        border-top: 0.1vh solid gray;
        border-right: 0.1vh solid gray;
        border-left: 0.1vh solid gray;
        
        
        
        //   
        position: fixed;
        bottom: 0; //   
        height: 6vh;
        width: 696px; //   

        //  
        box-shadow: 0 1px 5px rgba(0, 0, 0, 0.3);

        //    
        background:
        linear-gradient(to right, 
                transparent 0, 
                rgba(0, 0, 0, 0.3) 0.5px, 
                transparent 2px) 20% 50%,
            linear-gradient(to right, 
                transparent 0, 
                rgba(0, 0, 0, 0.3) 0.5px, 
                transparent 2px) 40% 50%,
            linear-gradient(to right, 
                transparent 0, 
                rgba(0, 0, 0, 0.3) 0.5px, 
                transparent 2px) 60% 50%,
            linear-gradient(to right, 
                transparent 0, 
                rgba(0, 0, 0, 0.3) 0.5px, 
                transparent 2px) 80% 50%;

        background-size: 1px 50%;
        background-repeat: no-repeat;
        background-color: white;
        .icon{
            //  
            display: flex;
            flex-direction: column;
            // 
            align-items: center;
            // 
            justify-content: center; 
    
            height: 100%;
            width: 20%;
            // background-color: gray;
            > p{
                margin: 0;
                padding: 0;
                font-size: 12px;
            }
            > img{
                height: 35px;
                width: 35px;

                // hover transform  
                transition: transform 0.2s ease
            }

            > img:hover{
                //  
                transform: scale(1.1);
                //   
                cursor: pointer;
            }
        }
    }
}